import React from "react"
import { Grid } from "semantic-ui-react"
import { useMediaQuery } from "react-responsive"
import chunk from "lodash/chunk"
import breakpoints from "../breakpoints"
import Option from "./ImageOption"

const Responsive = props => {
  const isDesktop = useMediaQuery({
    query: `(min-width: ${breakpoints.desktop.min}px)`,
  })

  return isDesktop ? (
    <Options numberOfChunks={3} {...props} />
  ) : (
    <Options numberOfChunks={2} {...props} />
  )
}

const Options = ({ numberOfChunks, dark, entityId, entities, onSelect }) => (
  <React.Fragment>
    {chunk(entities, numberOfChunks).map(chunks => (
      <Grid.Row columns="equal">
        {chunks.map(({ id, src, label }) => (
          <Grid.Column>
            <Option
              src={src}
              label={label}
              dark={dark}
              selected={entityId === id}
              onClick={() => onSelect(id)}
            />
          </Grid.Column>
        ))}
      </Grid.Row>
    ))}
  </React.Fragment>
)

export default Responsive
